"use client"
import { SITE_NAP } from "@/config/siteConfig"
import { Icon } from "@/app/components/Icon"
import { Link } from "@/app/utils/Link"
import { useEffect, useState } from "react"
import { MotionDiv } from "@/app/utils/lazy-ui"
import { ActivityCalendar, type Activity } from "react-activity-calendar"
import { format, subYears } from "date-fns"

interface LeetCodeData {
    totalSolved: number
    totalQuestions: number
    easySolved: number
    totalEasy: number
    mediumSolved: number
    totalMedium: number
    hardSolved: number
    totalHard: number
    ranking: number
    submissionCalendar: Record<string, number>
}

// Custom Donut Chart Component
const SolvedDonut = ({ easy, medium, hard, total, totalQuestions }: { easy: number, medium: number, hard: number, total: number, totalQuestions: number }) => {
    const radius = 40
    const circumference = 2 * Math.PI * radius

    // Calculate stroke format: [dashArray, dashOffset]
    // But for a simple stacked view, we usually just do segments. 
    // Here we will do a simple background + single progress or stacked? 
    // The user image shows a segmented arc. Let's try 3 arcs.

    // Easier approach: Conic gradient for the ring
    const totalS = easy + medium + hard
    const easyDeg = (easy / totalQuestions) * 360
    const mediumDeg = (medium / totalQuestions) * 360
    const hardDeg = (hard / totalQuestions) * 360

    // Actually the user image shows 'total solved' vs 'total questions' overall? 
    // Or maybe just the 3 categories making up the circle?
    // The image shows: 178/3821 Solved. And a yellow/teal ring.
    // Let's create a custom SVG with 3 paths for Easy/Medium/Hard.

    // Normalize logic for visibility
    const safeTotal = totalQuestions || 1
    const easyDash = (easy / safeTotal) * circumference
    const mediumDash = (medium / safeTotal) * circumference
    const hardDash = (hard / safeTotal) * circumference

    const gap = 2 // Gap between segments if we want, or 0.

    return (
        <div className="relative flex h-32 w-32 items-center justify-center">
            <svg className="h-full w-full -rotate-90 transform" viewBox="0 0 100 100">
                {/* Background track */}
                <circle cx="50" cy="50" r={radius} fill="none" stroke="#f3f4f6" strokeWidth="8" />

                {/* Easy Segment (Green) */}
                <circle
                    cx="50" cy="50" r={radius}
                    fill="none"
                    stroke="#10b981"
                    strokeWidth="8"
                    strokeDasharray={`${easyDash} ${circumference}`}
                    strokeLinecap="round"
                    className="opacity-80"
                />

                {/* Medium Segment (Yellow) - we need to offset it. SVG standard circle stroke starts at 3 o'clock usually, rotated -90 makes it 12 o'clock. 
                   With simple circles, stacking offset is hard without complex math.
                   Let's use a simpler Conic Gradient approach via CSS for perfect segments.
                */}
            </svg>
            {/* Text Overlay */}
            <div className="absolute inset-0 flex flex-col items-center justify-center text-center">
                <span className="text-2xl font-bold text-gray-900">{total}</span>
                <span className="text-[10px] text-gray-400">Solved</span>
            </div>
        </div>
    )
}

// Better Donut using Conic Gradient for simplicity and robustness
const CSSDonut = ({ easy, medium, hard, total }: { easy: number, medium: number, hard: number, total: number }) => {
    // Colors
    const cEasy = "#00b8a3"
    const cMed = "#ffc01e"
    const cHard = "#ef4743"
    const cTrack = "#eee"

    // Ratios assuming the circle represents Total Solved distributions (or Total Questions? usually Total Solved distribution in these charts)
    // Wait, LeetCode profile usually shows "Solved X / Total Y". 
    // The arc usually fills up based on Total Solved / Total Questions.
    // And the colors inside that arc represent the mix. 
    // That's complex. Let's simplify: A ring showing total progress, and separate breakdown.
    // Or just recreate the visual from the image roughly.
    // Image: "178/3821 Solved", large yellow arc. 

    return (
        <div className="relative flex h-36 w-36 items-center justify-center">
            {/* We will use a conic gradient for the breakdown of solved problems */}
            <div
                className="absolute inset-0 rounded-full"
                style={{
                    background: `conic-gradient(
                        ${cEasy} 0% ${(easy / total) * 100}%,
                        ${cMed} ${(easy / total) * 100}% ${((easy + medium) / total) * 100}%,
                        ${cHard} ${((easy + medium) / total) * 100}% 100%
                    )`,
                    mask: "radial-gradient(transparent 60%, black 61%)",
                    WebkitMask: "radial-gradient(transparent 60%, black 61%)" // Donut hole
                }}
            />
            <div className="z-10 flex flex-col items-center">
                <span className="text-3xl font-extra-bold text-gray-900">{total}</span>
                <span className="text-xs font-medium text-gray-500">Solved</span>
            </div>
        </div>
    )
}


export const LeetCodeStats = () => {
    const [stats, setStats] = useState<LeetCodeData | null>(null)
    const username = SITE_NAP.profiles.leetcode.split("/").pop() || "Mahir_R_Patell"

    useEffect(() => {
        // Use the proxy from config or direct
        fetch(`https://leetcode-stats-api.herokuapp.com/${username}`)
            .then(res => res.json())
            .then(data => {
                if (data.status === "success") {
                    setStats(data)
                }
            })
            .catch(err => console.error(err))
    }, [username])

    if (!stats) {
        return (
            <div className="h-full rounded-3xl border border-gray-200 bg-white p-8 shadow-xl">
                <div className="flex animate-pulse flex-col gap-4">
                    <div className="h-12 w-12 rounded-full bg-gray-200"></div>
                    <div className="h-4 w-32 bg-gray-200"></div>
                    <div className="h-32 w-full rounded-xl bg-gray-200"></div>
                </div>
            </div>
        )
    }

    // Process submission calendar
    // API returns unix timestamp as key (string) -> count
    const calendarData: Activity[] = []
    if (stats.submissionCalendar) {
        const today = new Date()
        const start = subYears(today, 1)

        Object.entries(stats.submissionCalendar).forEach(([ts, count]) => {
            const date = new Date(parseInt(ts) * 1000)
            if (date >= start) {
                calendarData.push({
                    date: format(date, "yyyy-MM-dd"),
                    count: count,
                    level: Math.min(4, Math.ceil(count / 2)) as 0 | 1 | 2 | 3 | 4 // simplistic level mapping
                })
            }
        })
    }

    // Fill gaps not needed for react-activity-calendar usually, it handles sparse data? 
    // Start/End date props.

    return (
        <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: 0.1 }}
            className="group relative overflow-hidden rounded-3xl border border-gray-100 bg-white p-8 shadow-xl transition-all hover:shadow-2xl"
        >
            <div className="relative z-10 flex flex-col gap-8">
                {/* Header */}
                <div className="flex items-center justify-between">
                    <div className="flex items-center gap-4">
                        <div className="relative">
                            <div className="absolute inset-0 rounded-full bg-yellow-400 opacity-20 blur-md"></div>
                            {/* LeetCode Icon SVG */}
                            <div className="relative flex h-12 w-12 items-center justify-center rounded-full bg-yellow-50 text-yellow-600">
                                <svg viewBox="0 0 24 24" fill="currentColor" className="h-8 w-8">
                                    <path d="M13.483 0a1.374 1.374 0 0 0-.961.438L7.173 5.423a1.398 1.398 0 0 0-.372 1.941 1.405 1.405 0 0 0 1.966.368l5.349-4.985a1.374 1.374 0 0 0 .445-.973 1.38 1.38 0 0 0-1.078-1.774Z"></path>
                                    <path d="M2.485 22.003a1.385 1.385 0 0 1-1.121-1.823l4.02-14.86a1.365 1.365 0 0 1 1.708-.942l.006.002a1.366 1.366 0 0 1 .949 1.696l-4.02 14.86a1.387 1.387 0 0 1-1.542.998v.069Z"></path>
                                    <path d="M21.527 22.067H6.938a1.387 1.387 0 0 1-1.384-1.392 1.39 1.39 0 0 1 1.384-1.39h14.589a1.39 1.39 0 0 1 1.384 1.39 1.39 1.39 0 0 1-1.384 1.392Z"></path>
                                </svg>
                            </div>
                        </div>
                        <div>
                            <h3 className="text-2xl font-bold text-gray-900">LeetCode</h3>
                            <p className="text-sm font-medium text-gray-500">Global Ranking: {stats.ranking.toLocaleString()}</p>
                        </div>
                    </div>
                    <Link
                        href={SITE_NAP.profiles.leetcode}
                        target="_blank"
                        className="hidden rounded-full border border-gray-200 bg-white px-5 py-2 text-sm font-medium text-gray-700 shadow-sm transition-all hover:-translate-y-0.5 hover:shadow-md sm:block"
                    >
                        View Profile
                    </Link>
                </div>

                <div className="flex flex-col gap-8 md:flex-row">
                    {/* Left Col: Donut Chart */}
                    <div className="flex flex-col items-center justify-center gap-4 rounded-3xl bg-gray-50 p-6 md:w-1/3">
                        <CSSDonut easy={stats.easySolved} medium={stats.mediumSolved} hard={stats.hardSolved} total={stats.totalSolved} />
                        <div className="text-sm text-gray-500">{stats.totalSolved} / {stats.totalQuestions} Solved</div>
                    </div>

                    {/* Right Col: Breakdown */}
                    <div className="flex flex-1 flex-col justify-center gap-4">
                        <div className="group/stat flex items-center justify-between rounded-xl bg-gray-50 p-3 px-4 transition-colors hover:bg-teal-50">
                            <span className="font-medium text-gray-600 group-hover/stat:text-teal-700">Easy</span>
                            <span className="font-bold text-gray-900 group-hover/stat:text-teal-700">{stats.easySolved} <span className="text-xs font-normal text-gray-400">/ {stats.totalEasy}</span></span>
                        </div>
                        <div className="group/stat flex items-center justify-between rounded-xl bg-gray-50 p-3 px-4 transition-colors hover:bg-yellow-50">
                            <span className="font-medium text-gray-600 group-hover/stat:text-yellow-700">Medium</span>
                            <span className="font-bold text-gray-900 group-hover/stat:text-yellow-700">{stats.mediumSolved} <span className="text-xs font-normal text-gray-400">/ {stats.totalMedium}</span></span>
                        </div>
                        <div className="group/stat flex items-center justify-between rounded-xl bg-gray-50 p-3 px-4 transition-colors hover:bg-red-50">
                            <span className="font-medium text-gray-600 group-hover/stat:text-red-700">Hard</span>
                            <span className="font-bold text-gray-900 group-hover/stat:text-red-700">{stats.hardSolved} <span className="text-xs font-normal text-gray-400">/ {stats.totalHard}</span></span>
                        </div>
                    </div>
                </div>

                {/* Submission Heatmap */}
                <div className="mt-2">
                    <p className="mb-4 text-sm font-semibold text-gray-900">Submission History</p>
                    <div className="overflow-x-auto pb-2 scrollbar-hide">
                        <div className="min-w-[600px]">
                            <ActivityCalendar
                                data={calendarData}
                                theme={{
                                    light: ['#ebedf0', '#fbbf24', '#f59e0b', '#d97706', '#b45309'],
                                    dark: ['#1f2937', '#fbbf24', '#f59e0b', '#d97706', '#b45309'], // Not using dark mode really but good to have
                                }}
                                hideColorLegend
                                hideMonthLabels={false}
                                hideTotalCount
                                blockSize={12}
                                blockMargin={4}
                                fontSize={12}
                            />
                        </div>
                    </div>
                </div>
            </div>
            {/* Decor element */}
            <div className="absolute -right-20 -top-20 h-64 w-64 rounded-full bg-gradient-to-br from-yellow-100 to-transparent opacity-50 blur-3xl pointer-events-none" />
        </motion.div>
    )
}
